.sb-pillbox__remove {
  --sb-button-color: var(
    --sb-badge-color-light,
    var(--sbt-theme-tertiary-color-20)
  );
  padding: 0;
  position: absolute;
  right: 6px;
  top: 50%;
  transform: translateY(-50%);
  width: 1em;

  &:hover {
    --sb-button-color: var(--sbt-theme-tertiary-color-10);
    --sb-button-color-light: transparent;
  }
}

.sb-pillbox__badge {
  padding-right: 25px;
}
